import React, { Component } from 'react';
import 'semantic-ui-css/semantic.min.css';
import point from "../../resources/img/p6.png";
import { Button, Divider } from 'semantic-ui-react';

import {
  Checkbox,
  Form,
  Input,
  Radio,
  Select,
  TextArea,
} from 'semantic-ui-react'


const m_value = '0'; //ablation, mapping, transspetal, induction and recording

const options = [
  { key: 's', text: 'SVT Ablation (93653)', value: 'svt ablate' },
  { key: 'v', text: 'VT Ablation (93654)', value: 'vt ablate' },
  { key: 'a', text: 'AF Ablation (93656)', value: 'af ablate' },
]


class Close1 extends Component {
  state = {}

  handleChange = (e, { value }) => this.setState({ value })

  render() {
    const { value } = this.state
    return (
    <div class="segment" align="center">
    <div class="grid" align="left" style={{width: 800}}>
      <Form>
   <h3 id="h3" align="left"> <img src={point} height={50} width={50} /> Closing Remarks  </h3>
   <Divider />
    <h3></h3>
  
        <Form.Field
          control={TextArea}
          label='Summary'
          placeholder='e.g. 62 yro male, BMI 32.5, hyperlipidemia 8+ yrs, hypertension 5+ yrs, undertook RF ablation (PVI+linear) 2019 @NYC Hospital, recurring Afib episodes started 3 months ago.'
        />
      </Form>
      </div>
      </div>
    )
  }
}

export default Close1